body {
    background-color: #191919;
    background-image: url(/onix-test2/static/media/boxer-background.27b2858a.jpg);
    background-repeat: no-repeat;
    background-size: auto;
    color: #EDEDED;
    font-family: 'Oswald', sans-serif;
    text-align: center;
    margin: 0;
}

/* Headings */

h1 {
    display: inline-block;
    font-family: 'Permanent Marker', sans-serif;
    font-size: 7rem;
    margin: 0;
}

h2 {
    font-size: 3rem;
    padding-top: 20px;
}

/*Other tags*/

p {
    font-size: 2rem;
    line-height: 20px;
}

li {
    line-height: 3rem;
    list-style-type: none;
}

h1:hover {
    color: #DA0037;
    text-decoration: none;
}

/* Resize */

@media (max-width: 1200px) {
    h1 {
        font-size: 3.5rem;
    }

    h2 {
        display: none;
    }

    h3 {
        font-size: 1rem;
    }

    p {
        font-size: 20px;
        line-height: normal;
        margin: 0;
    }

    ul {
        padding: 0;
    }
}
.biography-li {
    color: #DA0037;
    display: inline-block;
    font-size: 2rem;
    text-align: center;
    vertical-align: middle;
    width: 20%;
}

.biography-text-input {
    background-color: #EDEDED;
    border-radius: 20px;
    color: #880022;
    font-family: 'Oswald', sans-serif;
    font-size: 2rem;
    text-align: center;
    width: 10rem;
}

@media (max-width: 1200px) {
    .biography-text-input {
        font-size: 20px;
        width: 3rem;
    }
}
.boxer-photo {
    border: solid 4px #DA0037;
    border-radius: 50%;
    display: inline-block;
    margin: 10px;
    text-align: center;
    vertical-align: auto;
    width: 70%;
}

.biography-li {
    color: #DA0037;
    display: inline-block;
    font-size: 2rem;
    text-align: center;
    width: 20%;
}


@media (max-width: 1200px) {
    .belt {
        display: none;
    }

    .biography-li {
        font-size: 22px;
        width: 25%;
    }

    .boxer-photo {
        border: solid 2px;
        width: 60%;
    }
}
.biography-ul {
    background: #262323 linear-gradient(to right, #212121, #2c2c2c, #373737, #424242, #4e4e4e);
    border: solid #DA0037;
    border-radius: 30px;
    line-height: 0;
}

.biography-li {
    color: #DA0037;
    display: inline-block;
    font-size: 2rem;
    text-align: center;
    vertical-align: middle;
    width: 20%;
}

@media (max-width: 1200px) {
    .belt {
        display: none;
    }

    .biography-li {
        font-size: 22px;
        width: 25%;
    }
}
.biography-ul {
    background: #262323 linear-gradient(to right, #212121, #2c2c2c, #373737, #424242, #4e4e4e);
    border: solid #DA0037;
    border-radius: 30px;
    line-height: 0;
}

.selected-biography-ul,
.selected-biography-ul-white,
.selected-biography-ul-green,
.selected-biography-ul-blue {
    background: #262323 linear-gradient(to left, #212121, #2c2c2c, #373737, #424242, #4e4e4e);
    border: solid 5px #880022;
    border-radius: 30px;
}

.selected-biography-ul-white {
    border-color: #EDEDED;
}

.selected-biography-ul-green {
    border-color: #008000;
}

.selected-biography-ul-blue {
    border-color: #000045;
}
.big-red-btn {
    background: #DA0037 linear-gradient(180deg, #DA0037 30%, #880022 100%);
    border-radius: 10px;
    color: #EDEDED;
    display: inline-block;
    font-family: 'Oswald', sans-serif;
    font-size: 20pt;
    margin: 20px 30px 20px 30px;
    padding: 20px 60px;
    text-decoration: none;
}

.big-red-btn:hover {
    background: #880022 linear-gradient(180deg, #880022 10%, #DA0037 100%);
}

@media (max-width: 1200px) {
    .big-red-btn {
        font-size: 1rem;
        padding: 15px;
        margin: 10px;
    }
}
.boxer-link {
    color: #DA0037;
    text-decoration: none;
}

.boxer-link:hover {
    color: #880022;
}
.comment-list {
    align-items: center;
    border: solid #DA0037;
    border-radius: 20px;
    display: flex;
    margin: 50px 40px;
    line-height: 0;
}

.comment-preview {
    display: inline-block;
    text-align: center;
    margin-top: 15px;
    width: 20%;
}

.comment-text {
    display: inline-block;
    line-height: normal;
    align-items: center;
    margin: 10px 20px;
}

.comment-photo {
    border-radius: 100%;
    border: #DA0037 4px solid;
    height: 150px;
    width: 150px;
}

.comment-name {
    font-size: 1.2rem;
    margin: 0;
}

.comment-name:hover {
    color: #DA0037;
}

.left-aligned {
    background: #262323 linear-gradient(to right, #212121, #2c2c2c, #373737, #424242, #4e4e4e);
    display: flex;
    flex-direction: row-reverse;
    text-align: left;
}

.right-aligned {
    background: #262323 linear-gradient(to left, #212121, #2c2c2c, #373737, #424242, #4e4e4e);
    display: flex;
    text-align: right;
}
.section-title {
    font-family: 'Permanent Marker', sans-serif, sans-serif;
}

.section-title:hover {
    color: #DA0037;
}

#commentContainer {
    background: linear-gradient(180deg, #1f1f1f, #404040);
    border: 10px solid #DA0037;
    border-left: none;
    border-right: none;
    font-size: 1.5rem;
    margin-top: 20vh;
    padding-top: 3vh;
}
.call-to-sign-up {
    color: #DA0037;
    text-decoration: none;
    line-height: 5rem;
    margin-top: 1rem;
}

.main-text {
    font-size: 2rem;
}

@media (max-width: 1200px) {
    .call-to-sign-up {
        font-size: 1.5rem;
        line-height: 40px;
    }

    .main-text {
        font-size: 1.5rem;
    }
}
.link-icon {
    color: #DA0037;
    text-decoration: none;
    margin: 0 5px;
}

.link-icon:hover {
    color: #880022;
}
footer {
    background: #454545 linear-gradient(180deg, #222222 30%, #242424 100%);
}

.footer-info-menu {
    float: right;
    margin: 0 5% 2rem 0;
}
header {
    padding-top: 2rem;
}

